import { Box, Typography, useTheme } from "@mui/material";
import Friend from "components/FriendToggle";
import WidgetWrapper from "components/WidgetWrapper";
// import { useEffect } from "react";
// import { setFriends } from "state";
import { useSelector } from "react-redux";

const FriendListWidget = ({ userId }) => {
  // const dispatch = useDispatch();
  const { palette } = useTheme();
  // const token = useSelector((state) => state.token);
  const user = useSelector((state) => state.user);
  const users = useSelector((state) => state.users);
  const myFriends = useSelector((state) => state.user.friends);
  let friends;

  // const getFriends = async () => {
  //     const response = await fetch(
  //         `https://social-media-app-server.onrender.com/users/${userId}/friends`,
  //         {
  //             method: "GET",
  //             headers: { Authorization: `Bearer ${token}`}
  //         }
  //     );
  //     const data = await response.json();
  //     dispatch(setFriends({ friends: data }));
  // };

  // useEffect(() => {
  //     getFriends();
  // }, []); // eslint-disable-line react-hooks/exhaustive-deps

  if (userId === user._id) {
    friends = myFriends;
  } else {
    friends = users.find((user) => user._id === userId).friends;
  }

  return (
    <WidgetWrapper>
      <Typography
        color={palette.neutral.dark}
        variant="h5"
        fontWeight="500"
        sx={{ mb: "1.5rem" }}
      >
        Friend List
      </Typography>
      <Box display="flex" flexDirection="column" gap="1.5rem">
        {friends.map((friend) => (
          <Friend
            key={friend._id}
            friendId={friend._id}
            name={`${friend.firstName} ${friend.lastName}`}
            subtitle={friend.occupation}
            userPicturePath={friend.picturePath}
          />
        ))}
      </Box>
    </WidgetWrapper>
  );
};

export default FriendListWidget;
